import React from 'react';
import {getRepos, getUserData} from '../api/github-api';
import styled from 'styled-components';

const SearchBar = styled.div`
    input{
        background-color: hsla(0,0%,100%,.125);
        min-height: 26px;
        font-size: 16px;
        line-height: 20px;
        border: none;
        padding: 6px 8px;
        vertical-align: middle;
        border-radius: 3px;
        width: 70%;
        color: white;
    }
    button{
        background-color: #28a745;
        background-image: linear-gradient(-180deg,#34d058,#28a745 90%);
        color: #fff;
        -webkit-appearance: none;
        -webkit-user-select: none;
        appearance: none;
        background-position: -1px -1px;
        background-repeat: repeat-x;
        background-size: 110% 110%;
        border: 1px solid rgba(27,31,35,.2);
        border-radius: .25em;
        margin-left: 2%;
        cursor: pointer;
        display: inline-block;
        font-size: 14px;
        font-weight: 600;
        line-height: 20px;
        padding: 6px 12px;
        position: relative;
        user-select: none;
        vertical-align: middle;
        white-space: nowrap;
    }
    background-color: #24292e;
    color: hsla(0,0%,100%,.75);
    padding-bottom: 12px;
    padding-top: 12px; 
`;

const GitHubLogo = styled.svg`
    fill: white;
    vertical-align: middle;
    text-align: center;
    position: relative;
    padding: 0 20px;
`;


class Input extends React.Component{
    constructor(props){
        super(props);
        this.state = {};
    }
    inputChanged = (e) => this.setState({inputText: e.target.value.trim()});

    buttonClicked = () => {
        if(this.state.inputText){
            this.props.apiCall(getRepos, this.state.inputText);
            this.props.apiCall(getUserData, this.state.inputText);
        }
    };

    render(){
        return(
            <SearchBar>
                <a href="https://github.com/" data-hotkey="g d" aria-label="Homepage" data-ga-click="Header, go to dashboard, icon:logo">
                    <GitHubLogo height="32" viewBox="0 0 16 16" version="1.1" width="32" aria-hidden="true"><path fillRule="evenodd" d="M8 0C3.58 0 0 3.58 0 8c0 3.54 2.29 6.53 5.47 7.59.4.07.55-.17.55-.38 0-.19-.01-.82-.01-1.49-2.01.37-2.53-.49-2.69-.94-.09-.23-.48-.94-.82-1.13-.28-.15-.68-.52-.01-.53.63-.01 1.08.58 1.23.82.72 1.21 1.87.87 2.33.66.07-.52.28-.87.51-1.07-1.78-.2-3.64-.89-3.64-3.95 0-.87.31-1.59.82-2.15-.08-.2-.36-1.02.08-2.12 0 0 .67-.21 2.2.82.64-.18 1.32-.27 2-.27.68 0 1.36.09 2 .27 1.53-1.04 2.2-.82 2.2-.82.44 1.1.16 1.92.08 2.12.51.56.82 1.27.82 2.15 0 3.07-1.87 3.75-3.65 3.95.29.25.54.73.54 1.48 0 1.07-.01 1.93-.01 2.2 0 .21.15.46.55.38A8.013 8.013 0 0 0 16 8c0-4.42-3.58-8-8-8z"></path></GitHubLogo>
                </a>
                <input type="text" placeholder="Please enter github username..." onChange={this.inputChanged} />
                <button onClick={this.buttonClicked}>Search</button>
            </SearchBar>
        );
    }
}

export default Input;